import stringify from 'qs-stringify'
import { TaskActionTypes } from '../constants/Task'
import axios from '../../config/Axios'

export const LoadTodos = () => {
  return (dispatch) => {
    // first of all toggle isLoading var to display loading screen
    dispatch({ type: TaskActionTypes.SET_LOADING_STATE, payload: true })
    // make axios call to fetch all todos from server
    axios({
      url: '/api/todos',
      method: 'get'
    })
      .then((response) => {
        // push todos into state
        dispatch({
          type: TaskActionTypes.LOAD_TODOS,
          payload: response.data
        })
      })
      .catch((error) => {
        // set request error
        dispatch({
          type: TaskActionTypes.SET_DIALOG,
          payload: 'Failed to load todos.'
        })
      })
      .finally(() => {
        // set isLoading var in state back to false
        dispatch({ type: TaskActionTypes.SET_LOADING_STATE, payload: false })
      })
  }
}

export const ClearTodosArray = () => {
  return { type: TaskActionTypes.CLEAR_TODOS_ARRAY }
}

export const AddTodo = (body) => {
  return (dispatch) => {
    dispatch({ type: TaskActionTypes.SET_LOADING_STATE, payload: true })
    // create new todo on server
    axios({
      url: '/api/todos',
      method: 'post',
      headers: { 'content-type': 'application/x-www-form-urlencoded' },
      data: stringify({ todoBody: body })
    })
      .then((response) => {
        dispatch({
          type: TaskActionTypes.ADD_TODO,
          payload: response.data
        })
      })
      .catch((error) => {
        dispatch({
          type: TaskActionTypes.SET_DIALOG,
          payload: 'Failed to create new todo on server.'
        })
      })
      .finally(() => {
        // set isLoading var in state back to false
        dispatch({ type: TaskActionTypes.SET_LOADING_STATE, payload: false })
      })
  }
}

export const ToggleTodoDoneState = (id) => {
  return (dispatch) => {
    dispatch({ type: TaskActionTypes.SET_LOADING_STATE, payload: true })
    axios({
      url: `/api/todos/${id}`,
      method: 'patch'
    })
      .then((response) => {
        dispatch({ type: TaskActionTypes.TOGGLE_TODO_DONE_STATE, payload: id })
      })
      .catch((error) => {
        dispatch({
          type: TaskActionTypes.SET_DIALOG,
          payload: 'Failed to complete todo on server.'
        })
      })
      .finally(() => {
        // set isLoading var in state back to false
        dispatch({ type: TaskActionTypes.SET_LOADING_STATE, payload: false })
      })
  }
}

export const EditTodo = (id, body) => {
  return (dispatch) => {
    dispatch({ type: TaskActionTypes.SET_LOADING_STATE, payload: true })
    axios({
      url: `/api/todos/${id}`,
      method: 'patch',
      headers: { 'content-type': 'application/x-www-form-urlencoded' },
      data: stringify({ todoBody: body })
    })
      .then((response) => {
        dispatch({ type: TaskActionTypes.EDIT_TODO, payload: { id, body } })
      })
      .catch((error) => {
        dispatch({
          type: TaskActionTypes.SET_DIALOG,
          payload: 'Failed to edit todo on server.'
        })
      })
      .finally(() => {
        // set isLoading var in state back to false
        dispatch({ type: TaskActionTypes.SET_LOADING_STATE, payload: false })
      })
  }
}

export const ToggleTodoToBeDeletedState = (id) => {
  return { type: TaskActionTypes.TOGGLE_TODO_TO_BE_DELETED_STATE, payload: id }
}

export const DeleteTodoPermanently = (id) => {
  return (dispatch) => {
    axios({
      url: `/api/todos/${id}`,
      method: 'delete'
    })
      .then((response) => {
        dispatch({ type: TaskActionTypes.DELETE_TODO_PERMANENTLY, payload: id })
      })
      .catch((error) => {
        dispatch({
          type: TaskActionTypes.SET_DIALOG,
          payload: 'Failed to delete todo on server.'
        })
        // return back todo from `to be deleted state`
        dispatch({
          type: TaskActionTypes.TOGGLE_TODO_TO_BE_DELETED_STATE,
          payload: id
        })
      })
  }
}

export const CloseDialog = () => {
  return { type: TaskActionTypes.SET_DIALOG, payload: '' }
}
