import React from 'react'
import PropTypes from 'prop-types'
import PageTemplate from '../../PageTemplate'
import EmailVerificationProcessor from './EmailVerificationProcessor'

const EmailVerificationProcessorPage = ({ cancel }) => {
  return (
    <PageTemplate
      PageContent={<EmailVerificationProcessor cancel={cancel} />}
    />
  )
}

EmailVerificationProcessorPage.propTypes = {
  cancel: PropTypes.bool
}

export default EmailVerificationProcessorPage
